.categoryTable {
  width: 60%;
  margin: 50px auto;
  background-color: white;
  padding: 50px;
  border-radius: 10px;
  box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.065);
}
.categoryTable table {
  width: 100%;
  margin-top: 30px;
}
.categoryTable table thead th {
  background-color: darkcyan;
  color: white;
}

.categoryTable table tbody td {
  padding: 10px;
  text-align: center;
  vertical-align: middle;
}
.actionButtons button {
  margin: 0 10px;
}
